.header {
  position: relative;
  min-height: 43px;
  display: flex;
  justify-content: end;
  padding: 0 22px;
}

.header__nav {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.header__button {
  background-image: url(../img/icon/icon-menu.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 23px;
  height: 23px;
  background-color: transparent;
  margin-top: 10px;
}
