.button {
  font-family: inherit;
  font-style: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: block;

}

.button--primary-color {
  margin-left: auto;
  margin-right: -25px;
  background-color: #FFED00;
  min-width: 280px;
  height: 58px;
  border-radius: 10px 0px 0px 10px;
}

.button--secondary-color {
  background-color: #FF5959;
  margin-right: auto;
  margin-left: -25px;
  min-width: 280px;
  height: 58px;
  border-radius: 0 10px 10px 0;
  margin-top: 40px;
  color: #fff;
}
