.navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navigation__list-closed {
  display: none;
}

.navigation__item {
  width: 100%;
  max-height: 55px;
  text-align: center;
  padding: 19px 50px;
  background-color: #ff6e6e;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);

  &:first-child {
    position: relative;
  }
}

.navigation__link {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  padding: 20px;
}

.navigation__toggle {
  width: 22px;
  height: 22px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 24px;
}
